.menu-item {
    position: relative;
    padding: 17px 0;
    text-align: center;
    cursor: pointer;
    border: 1px solid transparent;
}

.menu-item.focused {
    background: -webkit-gradient(linear, left bottom, left top, from(#13202d), to(#0d1620));
    background: -webkit-linear-gradient(bottom, #13202d, #0d1620);
    background: -o-linear-gradient(bottom, #13202d, #0d1620);
    background: linear-gradient(0deg, #13202d, #0d1620);
    border-color: #0f222d;
    -webkit-box-shadow: inset 0 1px 0 #03060b;
            box-shadow: inset 0 1px 0 #03060b;
}

.menu-item .home,
.menu-item .messages,
.menu-item .image {
    display: inline-block;
    background: url('../../asserts/sprite.png');
}
.menu-item.focused .home,
.menu-item.focused .messages,
.menu-item.focused .image {
    background-position-x: -27px;
}

.menu-item .home {
    width: 26px;
    height: 26px;
}
.menu-item .messages {
    width: 27px;
    height: 28px;
    background-position-y: -26px;
}
.menu-item .image {
    width: 27px;
    height: 28px;
    background-position-y: -54px;
}

.menu-item .messages-count {
    position: absolute;
    right: 7px;
    top: 7px;

    width: 19px;
    height: 19px;

    border: 3px solid #2a3b4c;
    border-radius: 50%;
    background: -webkit-gradient(linear, left bottom, left top, from(#da0d14), to(#fc4a50));
    background: -webkit-linear-gradient(bottom, #da0d14, #fc4a50);
    background: -o-linear-gradient(bottom, #da0d14, #fc4a50);
    background: linear-gradient(0deg, #da0d14, #fc4a50);
    color: white;
    font-size: 12px;
}

.menu-item.focused .messages-count {
    border-color: #121f2c;
}

.menu-item .messages-count .text {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}


