.sidebar {
    min-width: 59px;
    
    border-top-width: 1px;
    border-right-width: 1px;
    border-style: solid;
    border-color: #172431;

    background: -webkit-gradient(linear, left bottom, left top, from(#243241), to(#2a3e52));

    background: -webkit-linear-gradient(bottom, #243241, #2a3e52);

    background: -o-linear-gradient(bottom, #243241, #2a3e52);

    background: linear-gradient(0deg, #243241, #2a3e52);
    -webkit-box-shadow: inset 0 1px 0 #41566a;
            box-shadow: inset 0 1px 0 #41566a;
}


